
.c {
    max-width: 748px;
    margin:0 auto;
    padding: 0 20px;
}

.header {
    background-color: darkblue;
}

.header_content {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
}
.header_text {
    color: white;
    text-transform: uppercase;
    font-size: 20px;
}
.burger_menu {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 4px;
    width: min-content;
}
.burger_line {
    width: 24px;
    height: 4px;
    background: white;
    border-radius: 10px;
}